.photobooth{
    height: 100dvh; // omfg thank you D
    // overflow-y: hidden;
}

.video{
    // transform: scaleX(-1);
    object-fit: cover;
    // aspect-ratio: 3/2;
    width: 100%;
    height: 100%;
    // height: 500px;
    background-color: orange;

    &-container{
        justify-self: center;
        background-color: lightgray;
        position: relative;
        aspect-ratio: 3/2;
        // min-height: 280px; // move this to big devices
        
        // width: min(100%, 150vh); // needed initially so it doesn't jump to size once all the refs are valid
        // line-height: 0; // PUT THIS BACK AFTER SET-TEXT IS ESTABLISHED!

        .flash{
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-color: white;
            opacity: 0;
            animation: flash 0.8s ease-out;

            @keyframes flash{
                from {
                    opacity: 0.75;
                }
            
                to {
                    opacity: 0;
                }
            }
        }

        .customize{
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            display: flex;
            background-color: rgba(0,0,0,0.4);
            -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    
            gap: 40px;
            padding: 16px 24px 24px;
    
            .container{
                display: flex;
                width: calc((100% - 40px) / 2 + 40px);
    
                & > *{
                    display: flex;
                    flex-direction: column;
                    gap: 8px;
                }
    
                .heading{color: white;}
                ul{
                    display: flex;
                    flex-flow: row wrap;
                    list-style-type: none;
                    gap: 24px;

                    input[type=radio]{display: none;}
                    input[type=radio]:checked + label{
                        background-color: orange;
                    }
                }
            }
        }
    }
}

.capture{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: center;

    gap: 40px;
    padding: 20px;
    
    .control{
        width: fit-content;
    }
    .container{
        display: flex;
        // &:first-child{justify-content: flex-end;}
        gap: 20px;
        // flex: 1;
        max-height: 200px;
        width: calc((100% - 40px) / 2 + 40px);
        figure{
            line-height: 0;
            max-width: calc((100% + 20px) / 2 - 20px);
            max-height: 80px;
            background-color: black;
            aspect-ratio: 3/2;
            img{
                width: 100%;
                // width: calc((100% + 20px) / 2 - 20px);
                // max-height: 80px;
                object-fit: cover;
                aspect-ratio: 3/2;
            }
        }
    }
}