}
.video-container {
  justify-self: center;
  background-color: lightgray;
  position: relative;
  aspect-ratio: 3/2;
}
.video-container .flash {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: white;
  opacity: 0;
  animation: flash 0.8s ease-out;
}
@keyframes flash {
  from {
    opacity: 0.75;
  }
  to {
    opacity: 0;
  }
}
.video-container .customize {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  gap: 40px;
  padding: 16px 24px 24px;
}
.video-container .customize .container {
  display: flex;
  width: calc((100% - 40px) / 2 + 40px);
}
.video-container .customize .container > * {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.video-container .customize .container .heading {
  color: white;
}
.video-container .customize .container ul {
  display: flex;
  flex-flow: row wrap;
  list-style-type: none;
  gap: 24px;
}
.video-container .customize .container ul input[type=radio] {
  display: none;
}
.video-container .customize .container ul input[type=radio]:checked + label {
  background-color: orange;
}

.capture {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-self: center;
  gap: 40px;
  padding: 20px;
}
.capture .control {
  width: -moz-fit-content;
  width: fit-content;
}
.capture .container {
  display: flex;
  gap: 20px;
  max-height: 200px;
  width: calc((100% - 40px) / 2 + 40px);
}
.capture .container figure {
  line-height: 0;
  max-width: calc((100% + 20px) / 2 - 20px);
  max-height: 80px;
  background-color: black;
  aspect-ratio: 3/2;
}
.capture .container figure img {
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  aspect-ratio: 3/2;
}

