}

.btn-icon, .btn-secondary, .btn-primary {
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease, color 0.2s, outline 0.2s;
  height: -moz-fit-content;
  height: fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.btn-icon i.fa, .btn-secondary i.fa, .btn-primary i.fa {
  transition: color 0.2s;
}

.btn-primary {
  gap: 8px;
  background: #1B7C5D;
  color: #FFF;
  text-decoration: none;
}
.btn-primary svg {
  fill: #FFF;
}
.btn-primary:active {
  background: #66A792;
  color: #FFF;
}

.btn-secondary {
  gap: 8px;
  outline-offset: -1px;
  background-color: white;
  color: black;
  text-decoration: none;
  padding: 8px 16px;
}
.btn-secondary svg {
  fill: #1B7C5D;
}
.btn-secondary:active {
  outline-color: #B7BDC2;
  background-color: #FCFCFC;
  color: #1B7C5D;
}

.btn-icon {
  line-height: 0;
  outline-offset: -2px;
  line-height: 0;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 100px;
  background: none;
  border: none;
  text-decoration: none;
  background-color: #000;
  width: 80px;
  height: 80px;
}
.btn-icon svg, .btn-icon i.fa {
  fill: white;
  color: white;
}
.btn-icon:active {
  background-color: #B7C5C8;
}
.btn-icon:active svg {
  fill: #EBB55F;
}

